import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

import { Navbar, Nav } from "react-bootstrap" 
import logo from '../images/logoFab3.svg';

import SignOut from "../containers/SignOut"



const Header = ({ siteTitle, logged }) => (
  <header>
    <Navbar className="fab-navbar">
      <div className="container fab-container-header">
        <Navbar.Brand href="/"> <img className="head-logo" src={logo} /></Navbar.Brand>
        <Nav className="mr-auto">
          <Link className="nav-link" disabled to="/courses/">Courses</Link>
          <a className="nav-link" target="blank" href="https://app.gitbook.com/@aztapps/s/cs101/">GitBook</a>
          {logged ?  <SignOut /> : ''}
        </Nav>
      </div>
    </Navbar>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
  logged: PropTypes.bool,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
