import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"


const LeftMenu = () => (
	<nav className="navbar navbar-expand navbar-dark bg-dark-fab flex-md-column flex-row align-items-start py-l">
	    <div className="collapse navbar-collapse align-items-start">
	        <ul className="flex-md-column flex-row navbar-nav w-100 justify-content-between fab-ul-bottom-menu">
	            <li className="nav-item nav-item-course">
	                <h3 className="nav-link pl-0 text-nowrap">CS101</h3>
	            </li>
	            <li className="nav-item mobile-menu-li">
	                <Link className="nav-link pl-0" to="/cs101/"><i className="ai-1"></i> <span className="d-none d-md-inline">Home</span></Link>
	            </li>
	        
	            <li className="nav-item mobile-menu-li">
	            	<Link className="nav-link pl-0" to="/cs101/rating/"><i className="ai-2"></i> <span className="d-none d-md-inline">Rating</span></Link>
	            </li>
	            <li className="nav-item mobile-menu-li">
	            	<Link className="nav-link pl-0" to="/cs101/homeworks"><i className="ai-3"></i> <span className="d-none d-md-inline">Homeworks</span></Link>
	            </li>
	            <li className="nav-item mobile-menu-li">
	            	<Link className="nav-link pl-0" to="/cs101/materials"><i className="ai-4"></i> <span className="d-none d-md-inline">Materials</span></Link>
	            </li>
	            <li className="nav-item mobile-menu-li">
	            	<Link className="nav-link pl-0" to="/cs101/projects"><i className="ai-5"></i> <span className="d-none d-md-inline">Projects</span></Link>
	            </li>
	            <li className="nav-item mobile-menu-li">
	            	<Link className="nav-link pl-0" to="/cs101/learn"><i className="ai-6"></i> <span className="d-none d-md-inline">Learn</span></Link>
	            </li>
	        </ul>
	    </div>
	</nav>
)


export default LeftMenu
